@import "../../../data/styles.css";
* {
	scroll-behavior: smooth;
}
.nav-container {
	margin: 0;
	display: flex;
	justify-content: center;
	align-items: center;
}

.navbar {
	display: flex;
	justify-content: center;
	align-items: center;
	position: fixed;
	top: 3vh;
	z-index: 999;
}

.nav-background {
	width: 450px;
	height: 40px;
	padding-left: 0%;
	padding-right: 0%;
	background: #fff;
	box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
	border-radius: 40px;
}

.nav-list {
	display: flex;
	justify-content: space-between;
	list-style: none;
	align-items: center;
	margin-left: -16px;
	margin-right: 20px;
	margin-top: 11px;
}

.nav-item {
	font-weight: bold;
	font-size: 80%;
}
.about.section {
	font-weight: bold;
	font-size: 80%;
}
.nav-item.active a {
	color: var(--link-color) !important;
}
.about.section.active a {
	color: var(--link-color) !important;
}
.about.section a {
	text-decoration: none;
	color: var(--primary-color);
}
.nav-item a {
	text-decoration: none;
	color: var(--primary-color);
}
.nav-item button {
	/* border: 1px solid red; */
	font-weight: bold;
	border: none;
	background: #fff;
	color: var(--primary-color);
}
.nav-item button:hover {
	/* border: 1px solid var(--link-color); */
	/* color: var(--link-color); */
	color: #ba68c8;
	transition: color 0.3s ease-in-out;
	cursor: pointer;
}
.nav-item a:hover {
	/* color: var(--link-color);
	 */
	color: #ba68c8;

	transition: color 0.3s ease-in-out;
}
.about.section a:hover {
	color: #ba68c8;

	/* color: var(--link-color); */
	transition: color 0.3s ease-in-out;
}
@media (max-width: 600px) {
	.navbar {
		margin-left: 25%;
		margin-right: 25%;
		width: 80%;
		font-size: 80%;
	}

	.nav-background {
		width: 80%;
		height: 40px;
	}

	.nav-item {
		font-weight: bold;
	}
}
