@import "../../../data/styles.css";

.project-card {
	/* background: #fafafa; */
	mix-blend-mode: normal;
	border-radius: 20px;
	opacity: 0.8;
	height: 100%;
}

.project-card a {
	text-decoration: none;
}
/* #fafafa */
.project-card:hover {
	background: #f3e5f5;
	opacity: 1;
	transition: background-color 0.3s ease-in-out;
}

.project-card:hover .project-link {
	color: var(--link-color);
	transition: color 0.3s ease-in-out;
}

.project-container {
	padding: 30px;
}

.project-logo {
	width: 100%;
}

.project-logo img {
	width: 100%;
}

.project-title {
	font-family: var(--secondary-font);
	color: var(--primary-color);
	font-size: 16px;
	padding-top: 15px;
	margin-bottom: 1em;
	font-weight: 700;
}
.project-tech-stack {
	/* width: 90%; */
	margin-top: 10px;
	display: grid;
	grid-template-columns: repeat(4, 1fr);

	/* position: fixed; */
	/* justify-content: space-around; */
}
.project-github-link {
	margin-top: 10px;
}
.project-tech-stack > p {
	color: var(--secondary-color);

	font-size: 12px;
	font-weight: 700;
}
.project-description {
	color: var(--secondary-color);
}
.github-link {
	display: flex;
	justify-content: space-around;
	align-items: end;
	margin-top: 10px;
}
.project-link {
	display: flex;
	align-items: center;
	color: var(--secondary-color);
	font-size: 12px;
	padding-top: 20px;
}

.project-link-icon {
	padding-left: 5px;
	font-size: 13px;
}

.project-link-text {
	padding-left: 20px;
	font-weight: 700;
}

@media (max-width: 600px) {
	.project {
		height: 300px;
	}
}
