.articles-logo-container {
	display: flex;
	justify-content: left;
	padding-top: 25px;
}

.articles-logo {
	display: flex;
	position: fixed;
	border: 1px solid white;
	border-radius: 50%;
	box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
	top: 4vh;
}

.articles-main-container {
	display: flex;
	flex-direction: column;
	height: 100%;
	margin: 0;
	padding-top: 120px;
}

.articles-container {
	display: flex;
	flex-direction: column;
}

.articles-wrapper {
	width: 100%;
	padding-top: 50px;
	padding-bottom: 50px;
	display: grid;
	gap: 10px;
	/* flex-direction: column; */
	grid-template-columns: repeat(5, 1fr);
}
.skill-skill {
	width: 100%;
	display: flex;
	flex-direction: column;
	/* border: 1px solid red; */
	/* margin: auto; */
}
.skill-skill > div {
	width: 100%;
	justify-content: center;
}
.skill-skill > h4 {
	text-align: center;
}
.skill-skill > img {
	width: 40%;
	display: inline;
	margin: auto;

	/* padding-top: 0px;
	padding-left: 35px;
	padding-bottom: 20px;
	border-left: 2px solid #f4f4f5; */
	/* width: 100%; */
}

@media (max-width: 1024px) {
	.articles-title {
		width: 100% !important;
	}

	.articles-subtitle {
		width: 100% !important;
	}
}
